import 'bootstrap/dist/css/bootstrap.min.css';
import React from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Board from './Board';
import ScoreBoard from './ScoreBoard';
import { createGame } from './GameLogic';
import { modes } from './Util';
import { Button, ButtonGroup, Container, Navbar, Offcanvas, ProgressBar } from 'react-bootstrap';
import { ArrowCounterclockwise, XLg } from 'react-bootstrap-icons';

import './index.css';
import './style.scss';

class Game extends React.Component {
  constructor(props) {
    super(props);

    let gameSettings = localStorage.getItem('gameSettings') ? JSON.parse(localStorage.getItem('gameSettings')) : JSON.parse('{"showSettings":false,"gameMode":"defaultGame","colorMode":"defaultColor","numberMode":"defaultNumber","bonusMode":"noBonus"}');
    let game = localStorage.getItem('gameData') ? JSON.parse(localStorage.getItem('gameData')) : createGame(4, gameSettings);
    let gameHistory = localStorage.getItem('gameHistory') ? JSON.parse(localStorage.getItem('gameHistory')) : null;

    this.state = {
      game: game,
      gameHistory: gameHistory,
      gameSettings: gameSettings,
    }
    console.log(this.state);
  }

  handleClick(row, col, bonusClick = false) {
    console.log("Row: " + (row+1) + ", Col: " + (col+1) + " clicked");

    const current = this.state;
    const size = modes.gameMode.values[current.gameSettings.gameMode].cols;

    // If clicked field is locked do nothing, i.e. return
    if (current.game.rows[row].values[col].locked || current.game.rows[row].values[col].marked) {
      return;
    }

    // Save game history
    if (!bonusClick) {
      current.gameHistory = current.game;
      localStorage.setItem('gameHistory', JSON.stringify(current.game));
    }

    if ( (size === 12 && col === 10) || ( (size === 16) && (col === 13 || col === 14) ) ) {
      // The penultimate column (defaultGame) or the second to last two columns (longoGame) may only be clicked
      // if there are at least five marks existing in this row.
      if ((size === 12 && current.game.rows[row].marks > 4) || (size === 16 && current.game.rows[row].marks > 5)) {
        // Mark the clicked column
        current.game.rows[row].values[col].marked = !current.game.rows[row].values[col].marked;

        // Also mark the final column
        current.game.rows[row].values[size-1].marked = !current.game.rows[row].values[size-1].marked;

        // Add two marks, one for the clicked and one for the final
        current.game.rows[row].marks += 2;

        // Lock the current row and count number of locked rows up
        toggleLock(true, row, size); // Lock all columns in the row so no return on that one
        current.game.lockedRows += 1;
      } else {
        return;
      }

    } else if ( col === (size-1) ) {
      // If the ultimate column is clicked, everything is locked and no marks are added
      current.game.rows[row].values[col].marked = !current.game.rows[row].values[col].marked;

      if (current.game.rows[row].values[col].marked) {
        current.game.lockedRows += 1;
        toggleLock(true, row, size);
      } else {
        current.game.lockedRows -= 1;
        toggleLock(false, row, size);
      }
    } else {
      // If any other column is clicked there is a mark set
      current.game.rows[row].values[col].marked = !current.game.rows[row].values[col].marked;
      if (current.game.rows[row].values[col].marked) {
        current.game.rows[row].marks += 1;
        toggleLock(true, row, col);
      } else {
        current.game.rows[row].marks -= 1;
        toggleLock(false, row, col);
      }
    }
    
    // If mark was set on a bonus field, set further mark
    if (current.game.rows[row].values[col].bonus) {
      let bonusRowActivatedCol = current.game.bonusRow.values.filter((val) => val.marked === false)[0];
      current.game.bonusRow.values[bonusRowActivatedCol.id].marked = true;

      let nextBonusMark = {
        row: bonusRowActivatedCol.colorId,
        col: current.game.rows[bonusRowActivatedCol.colorId].values.filter((val) => val.marked === false && val.locked !== true)[0].id
      };

      this.handleClick(nextBonusMark.row, nextBonusMark.col, true);

      // current.game.rows[nextBonusMark.row].values[nextBonusMark.col].marked = !current.game.rows[nextBonusMark.row].values[nextBonusMark.col].marked;

      // // If the clicked field is not yet marked, set the mark on the color bonus field (First unmarked field)
      // if (!current.game.rows[row].values[col].marked) {
      //   let bonusRowActivatedCol = current.game.bonusRow.values.filter((val) => val.marked === false)[0]
      //   current.game.bonusRow.values[bonusRowActivatedCol.id].marked = true;
      //   console.log(bonusRowActivatedCol);
      //   // set mark for bonus
      //   // Get last marked bonus color and set mark in the respective row

      // // If the field is marked, remove the last bonus field of the color bonus row (Last marked field) - no longer needed
      // } else {
      //   const markedBonusFields = current.game.bonusRow.values.filter((val) => val.marked === true);
      //   current.game.bonusRow.values[markedBonusFields[markedBonusFields.length-1].id].marked = false;
      // }
    }

    if (current.game.lockedRows === 2 || current.game.rows[current.game.rows.length-1].marks === 4) {
      console.log("GameOver");
      current.game.finished = true;
    }

    this.setState({
      game: current.game,
      gameHistory: current.gameHistory
    });

    localStorage.setItem('gameData', JSON.stringify(current.game));

    function toggleLock(lock, row, col) {
      // Locking and unlocking is not done for the last row (Fails don't need to be locked)
      if (row !== (current.game.rows.length-1) ) {
        // Loop from clicked column backwards to first column
        let counter = col;
        while (counter !== 0) {
          counter--; // Don't touch current column
          current.game.rows[row].values[counter].locked = lock; // Lock/Unlock
          if (!lock && current.game.rows[row].values[counter].marked) {
            // if we unlock and the first unlocked column is marked we stop so all the others remain locked
            break;
          }
        }
      }

      if (col === size && current.gameSettings.bonusMode === "colorBonus") {
        // console.log("Lock Bonus Color:" + current.game.rows[row].values[0].color);
        for (const obj of current.game.bonusRow.values) {
          if (obj.color === current.game.rows[row].values[0].color) {
            obj.marked = true;
          }
        }
      }
    }
  }

  handleButtonClick(event) {
    if (event.currentTarget.id === "new") {
      localStorage.removeItem('gameData');
      localStorage.removeItem('gameHistory');
      const gameSettings = this.state.gameSettings;
      gameSettings.showSettings = false;
      this.setState({
        game: createGame(4, gameSettings),
        gameHistory: null,
        gameSettings: gameSettings
      });

      localStorage.setItem("gameSettings", JSON.stringify(gameSettings));
    }

    if (event.currentTarget.id === "undo" || event.currentTarget.id === "undoMain") {
      if (localStorage.getItem('gameHistory')) {
        const previousGame = JSON.parse(localStorage.getItem('gameHistory'));

        this.setState({
          game: previousGame,
          gameHistory: null
        });

        localStorage.setItem("gameData", JSON.stringify(previousGame));
        localStorage.removeItem("gameHistory");

        const gameSettings = this.state.gameSettings;
        gameSettings.showSettings = false;
      }
    }
  }

  handleChange(event) {
    const game = this.state.game;
    const gameSettings = this.state.gameSettings;

    gameSettings[event.target.name] = event.target.id;

    this.setState({
      game: game,
      gameSettings: gameSettings,
    });
    localStorage.setItem("gameSettings", JSON.stringify(gameSettings));
  }

  toggleSettings(current) {
    current.showSettings = !current.showSettings;
    this.setState({
      gameSettings: current,
    });
    localStorage.setItem("gameSettings", JSON.stringify(current));
  }

  calculateProgressBarValues(rows) {
    let sumOfMarks = rows[0].marks + rows[1].marks + rows[2].marks + rows[3].marks;
    let retVal = [25, 25, 25, 25];

    if (sumOfMarks !== 0) {
      for (var count = 0; count < 4; count++) {
        retVal[count] = (rows[count].marks * 25) / (sumOfMarks/4)
      }
    }

    return retVal;
  }

  render() {
    const current = this.state;
    const pbv = this.calculateProgressBarValues(current.game.rows);
    const bonusRow = [];

    if (current.game.bonusRow) {
      const bonusRowCols = [];    

      for (var bonus of current.game.bonusRow.values) {
        let classesOuter = "diceContainer edge " + bonus.color;
        let classesInner = "diceBox rounded " + bonus.color;
        let content = bonus.marked ? <XLg size={38} /> : "";
  
        bonusRowCols.push(
          <Col className={classesOuter} key={bonus.key}>      
            <div className={"none"}>
              <div className={classesInner}>
                <div className="diceBoxContent text-truncate">{content}</div>
              </div>
            </div>
          </Col>
        );
      }

      bonusRow.push(
        <Container fluid>
          <Row>
            {bonusRowCols}
          </Row>          
        </Container>
      );
    }

    return (
      <>
        <Navbar key="false" variant="dark" bg="dark" expand="false" className="mb-3">
          <Container fluid>
            <Navbar.Brand href="#">DiceSheet</Navbar.Brand>
            <ButtonGroup className='diceOrange' style={{display: current.game.rows[0].values.length > 12 ? "flex" : "none"}}>
              <Button disabled variant="outline-dark">{current.game.ext[0].firstBonusNumber}</Button>
              <Button disabled variant="outline-dark">{current.game.ext[0].secondBonusNumber}</Button>
            </ButtonGroup>
            <ButtonGroup>
              <Button style={{visibility: current.gameHistory !== null ? "" : "hidden"}} variant='outline-warning' id='undoMain' onClick={(e) => this.handleButtonClick(e)}><ArrowCounterclockwise /></Button>
              <Navbar.Toggle aria-controls="offcanvasNavbar-expand-false" onClick={() => this.toggleSettings(current.gameSettings)} />
            </ButtonGroup>
            <Navbar.Offcanvas
              id="offcanvasNavbar-expand-false"
              aria-labelledby="offcanvasNavbarLabel-expand-false"
              placement="end"
              show={current.gameSettings.showSettings || current.game.finished}
              onHide={() => this.toggleSettings(current.gameSettings)}
            >
              <Offcanvas.Header closeButton={current.game.finished ? false : true}>
                <Offcanvas.Title id="offcanvasNavbarLabel-expand-false">
                  {current.game.finished ? "Game Over" : "Optionen"}
                </Offcanvas.Title>
              </Offcanvas.Header>
              <Offcanvas.Body>
                <ScoreBoard
                  current={current}
                  handleChange={(e) => this.handleChange(e)}
                  onClick={(e) => this.handleButtonClick(e)}
                />
              </Offcanvas.Body>
            </Navbar.Offcanvas>
          </Container>
        </Navbar>

        <Container fluid className='text-center'>
          <Board
            rows={current.game.rows}
            onClick={(row,col) => this.handleClick(row,col)}
            settings={current.gameSettings}
          />
        </Container>

        <Container fluid>
          <div className='mt-2 mb-4'>
            <ProgressBar>
              <ProgressBar label={current.game.rows[0].marks} variant="danger" now={pbv[0]} />
              <ProgressBar label={current.game.rows[1].marks} variant="warning" now={pbv[1]} />
              <ProgressBar label={current.game.rows[2].marks} variant="success" now={pbv[2]} />
              <ProgressBar label={current.game.rows[3].marks} variant="primary" now={pbv[3]} />
            </ProgressBar>
          </div>
        </Container>

        {bonusRow}
      </>
    );
  }
}

export default Game;
