import 'bootstrap/dist/css/bootstrap.min.css';
import React from 'react';
import { modes, getPoints, numbers } from './Util';
import { Badge, Button, ButtonGroup, Form, Table } from 'react-bootstrap';
import { ArrowCounterclockwise, Trash } from 'react-bootstrap-icons';


class ScoreBoard extends React.Component {
  renderRadio(optionName, label, id, currentMode) {
    return(
      <Radio
        name={optionName}
        id={id}
        key={id}
        label={label}
        currentMode={currentMode}
        handleChange={(e) => this.props.handleChange(e)}
      />
    );
  }

  render() {
    const options = [];
    const gameButtons = [];

    // Create options
    for (var modeKey in modes) {
      var mode = modes[modeKey];
      let radios = [];
      for (let typeKey in mode.values) {
        var type = mode.values[typeKey];
        // console.log(modeKey);
        // console.log(type);
        radios.push(this.renderRadio(modeKey, type.name, type.id, this.props.current.gameSettings[modeKey]));
      }

      options.push(
        <>
          <Form.Label key={mode.title} className='mt-2' htmlFor={modeKey}>{mode.title}</Form.Label>
          <div key={mode.title+"Container"} className="btn-group w-100" role="group">
            {radios}
          </div>
        </>
      );
    }

    // Create Game Buttons, if there is a history, show new game and undo button, else show only new game
    if (this.props.current.gameHistory == null) {
      gameButtons.push(
        <Button className='d-flex flex-row mt-4' id="new" variant="danger" onClick={(e) => this.props.onClick(e)}>
          <div className='p-2'>Neues Spiel</div>
          <div className='p-2'><Trash /></div>
        </Button>
      );
    } else {
      gameButtons.push(
        <ButtonGroup>
          <Button className='d-flex flex-row mt-4' id="new" variant="danger" onClick={(e) => this.props.onClick(e)}>
            <div className='p-2'>Neues Spiel</div>
            <div className='p-2'><Trash /></div>
          </Button>
          <Button className='d-flex flex-row mt-4 hidden' id="undo" variant="outline-danger" onClick={(e) => this.props.onClick(e)}>
            <div className='p-2'>Rckgngig</div>
            <div className='p-2'><ArrowCounterclockwise /></div>
          </Button>
        </ButtonGroup>
      );
    }

    return (
      <>
        <Table bordered cellSpacing={"2em"}>
          <tbody>
            {getPoints(this.props.current.game.rows).map( (score, index, points) =>
              <tr key={score.id}>
                <th>{score.id + ":"}</th>
                <td>{((score.marks < 13) ? numbers[score.marks] : score.marks + " Kreuze")}</td>
                <td><Badge bg={(score.points >= 0) ? "success" : "danger"} pill>{score.points} {score.points === 1 ? "Punkt" : "Punkte"}</Badge></td>
              </tr>
            )}
          </tbody>
        </Table>
        <div className='d-grid gap-2'>
          {options}
          {gameButtons}
        </div>      
      </>
    );
  }
}

function Radio(props) {
  // short (default,longo); name (gameMode, colorMode); id (defaultGame, longoGame, defaultNumber) currentMode (defaultGame, longoGame)
  // Params for handleChange, event, game, gameSetting
  let buttonStyle;
  switch (props.name) {
    case "gameMode":
      buttonStyle = "btn btn-outline-primary";
      break;
    case "colorMode":
      buttonStyle = "btn btn-outline-success";
      break;
    case "bonusMode":
      buttonStyle = "btn btn-outline-purple";
      break;
    case "numberMode":  
    default:
      buttonStyle = "btn btn-outline-gamboge";
      break;
  }

  return (
    <>
      <input type="radio" className="btn-check" name={props.name} id={props.id} key={props.id}
             autoComplete="off" checked={props.currentMode === props.id} onChange={props.handleChange} />
      <label className={buttonStyle} htmlFor={props.id}>{props.label}</label>
    </>
  );
}

export default ScoreBoard;
