export function createGameColors(gameScheme) {
  var gameColors = gameScheme.map((v, i, a) => []);

  for (var rowIndex = 0; rowIndex < gameScheme.length; rowIndex++) {
    for (var colIndex = 0; colIndex < gameScheme[rowIndex].length; colIndex++) {
      var counter = gameScheme[rowIndex][colIndex].amount;
      if (counter === 0) {
        continue;
      } else {
        while (counter > 0) {
          // console.log("Row: " + (rowIndex+1) + " Col: " + (colIndex+1) + " value: " + counter + " color: " + gameScheme[rowIndex][colIndex].color);
          gameColors[rowIndex].push(gameScheme[rowIndex][colIndex].color);
          counter--;
        }
      }
    }
  }

  return gameColors;
}

export function sum(uptoX) {
  var result = 0;
  while (uptoX !== 0) {
    result += uptoX;
    uptoX -= 1;
  }

  return result;
}

export function shuffleArray(size) {
  let retVal = Array(size-1);
  let number = 2;
  for (var count = 0; count < retVal.length; count++) {
    retVal[count] = number;
    number++;
  }

  for (let i = retVal.length - 1; i > 0; i--) {
    let j = Math.floor(Math.random() * (i + 1)); // random index from 0 to i

    // swap elements array[i] and array[j]
    // we use "destructuring assignment" syntax to achieve that
    // you'll find more details about that syntax in later chapters
    // same can be written as:
    // let t = array[i]; array[i] = array[j]; array[j] = t
    [retVal[i], retVal[j]] = [retVal[j], retVal[i]];
  }

  return retVal;
}

export function getBonusNumbers() {
  return shuffleArray(16).slice(0, 2);
}

export function getPoints(rows) {
  let points = [];
  let total = 0;
  let marks = 0;
  for (let row of rows) {
    const current = {
      "id": "Reihe " + (row.id+1),
      "points": 0,
      "marks": row.marks
    };
    marks += row.marks;
    if (row.id !== rows.length-1) {
      total += sum(row.marks);
      current.points = sum(row.marks);
    } else {
      total -= (row.marks * 5);
      current.id = "Fehlwrfe";
      current.points = row.marks === 0 ? 0 : -(row.marks * 5);
    }

    points.push(current);
  }

  points.push({
    "id": "Gesamt",
    "points": total,
    "marks": marks
  });

  return points;
}
