import React from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Lock, Unlock, XLg } from 'react-bootstrap-icons';

class Board extends React.Component {
  renderSquare(row, col) {
    return (
      <Square
          key={col}
          id={col}
          content={this.props.rows[row].values[col].content}
          onClick={() => this.props.onClick(row,col)}
          color={this.props.rows[row].values[col].color}
          marked={this.props.rows[row].values[col].marked}
          locked={this.props.rows[row].values[col].locked}
          row={row}
          rowSize={this.props.rows.length}
          length={this.props.rows[row].values.length}
          bonus={this.props.rows[row].values[col].bonus}
        />
    );
  }

  render() {
    const board = [];

    // Create rows
    for (var row of this.props.rows) {
      let cols = [];
      for (let col of row.values) {
        cols.push(this.renderSquare(row.id, col.id));
      }

      board.push(
        <Row md={12} key={row.id}>
          {cols}
        </Row>
      );
    }

    return (
      <>
        {board}        
      </>
    );
  }
}

function Square(props) {
  let classesOuter = "diceContainer edge " + props.color;
  if (props.row < 4 && (props.locked || props.marked)) {
    classesOuter += " locked";
  }
  let classesInner = (props.row < 4 && props.id === (props.length-1)) ? "diceBox round " + props.color : "diceBox rounded " + props.color;
  if (props.bonus) {
    classesInner += " diceBonus";
  }
  let content;

  if ( props.row < 4 && props.id === (props.length-1) ) {
    content = props.marked ? <Lock /> : <Unlock />;
  } else {
    content = props.marked ? <XLg size={38} /> : props.content;
  }

  return (
    <Col className={classesOuter}>      
      {(props.row < 4 && props.id === 0) ? <div><i className="arrow right"></i></div> : ""}
      <div className={props.row < 4 && props.id === (props.length-1) ? "roundBox" : "none"}>
        <div className={classesInner} onClick={ props.onClick }>
          <div className="diceBoxContent text-truncate">{content}</div>
        </div>
      </div>
    </Col>
  );
}

export default Board;
