import { modes, createGameColors, shuffleArray, getBonusNumbers } from './Util.js';

export function createGame(size, settings) { // (size, colors, style) -> (size, settings.colorMode, settings.numberMode)
  let mixxedNumbers = [
    [10, 6, 2, 8, 3, 4, 12, 5, 9, 7, 11],
    [9, 12, 4, 6, 7, 2, 5, 8, 11, 3, 10],
    [8, 2, 10, 12, 6, 9, 7, 4, 5, 11, 3],
    [5, 7, 11, 9, 12, 3, 8, 10, 2, 6, 4]
  ]

  let mixxedLongoNumbers = [
    [10, 6, 2, 15, 8, 3, 14, 4, 16, 12, 5, 9, 13, 7, 11],
    [9, 12, 4, 16, 6, 7, 13, 2, 14, 5, 8, 11, 15, 3, 10],
    [8, 2, 10, 14, 12, 6, 15, 9, 14, 7, 4, 5, 16, 11, 3],
    [5, 7, 14, 11, 9, 12, 16, 3, 15, 8, 10, 2, 14, 6, 4]
  ]

  let bonusArray = [0,1,2,3,2,0,3,1,0,1,3,2,1,2,3,0];

  let gameStyle = getGameStyle(settings.numberMode);
  let maxCols = modes.gameMode.values[settings.gameMode].cols;

  function getNo(number) {
    let retVal = number;
    
    if (maxCols === 16) {
      retVal += 1;
    }

    return retVal;
  }

  let gameColors = {
    "defaultColor": [
      [{ "color": "diceRed", "amount": maxCols }, { "color": "diceYellow", "amount": 0 },  { "color": "diceGreen", "amount": 0 }, { "color": "diceBlue", "amount": 0 }],
      [{ "color": "diceRed", "amount": 0 }, { "color": "diceYellow", "amount": maxCols },  { "color": "diceGreen", "amount": 0 }, { "color": "diceBlue", "amount": 0 }],
      [{ "color": "diceRed", "amount": 0 }, { "color": "diceYellow", "amount": 0 },  { "color": "diceGreen", "amount": maxCols }, { "color": "diceBlue", "amount": 0 }],
      [{ "color": "diceRed", "amount": 0 }, { "color": "diceYellow", "amount": 0 },  { "color": "diceGreen", "amount": 0 }, { "color": "diceBlue", "amount": maxCols }]
    ],
    "mixxedColor": [
      [{ "color": "diceYellow", "amount": getNo(3) }, { "color": "diceBlue", "amount": getNo(3) },  { "color": "diceGreen", "amount": getNo(3) }, { "color": "diceRed", "amount": getNo(3) }],
      [{ "color": "diceRed", "amount": getNo(2) }, { "color": "diceGreen", "amount": getNo(4) },  { "color": "diceBlue", "amount": getNo(2) }, { "color": "diceYellow", "amount": getNo(4) }],
      [{ "color": "diceBlue", "amount": getNo(3) }, { "color": "diceYellow", "amount": getNo(3) },  { "color": "diceRed", "amount": getNo(3) }, { "color": "diceGreen", "amount": getNo(3) }],
      [{ "color": "diceGreen", "amount": getNo(2) }, { "color": "diceRed", "amount": getNo(4) },  { "color": "diceYellow", "amount": getNo(2) }, { "color": "diceBlue", "amount": getNo(4) }]
    ]
  }

  let gameNumbers;

  let colorPanel = createGameColors(gameColors[settings.colorMode]);

  var game = {
    "rows": [],
    "ext": [],
    "lockedRows": 0,
    "finished": false,
  };

  // Bonuses
  let bonusNumbers = getBonusNumbers();
  var ext = [
    {
      "id": "longoBonus",
      "title": "Bonus-Zahlen",
      "firstBonusNumber": bonusNumbers[0],
      "secondBonusNumber": bonusNumbers[1]
    }
  ]
  game.ext = ext;  

  // Create Rows and fill them.
  for (var rowIndex = 0; rowIndex < size; rowIndex++) {
    // Create row to add to game
    let row = {
      "id": rowIndex,
      "marks": 0,
      "locked": false,
      values: []
    };

    gameNumbers = shuffleArray(maxCols);

    // Fill rows values array with content
    for (var colIndex = 0; colIndex < maxCols; colIndex++) {
      row.values[colIndex] = {
        "id": colIndex,
        "color": colorPanel[rowIndex][colIndex],
        "content": genVal(colIndex, gameStyle[rowIndex], rowIndex),
        "marked": false,
        "locked": false,
        "bonus": getBonusMark(rowIndex, colIndex)
      };
    }

    // Add row to the game
    game.rows[rowIndex] = row;
  }

  // Create fails to track failed dices
  let fails = {
    "id": size,
    "color": "diceGray",
    "marks": 0,
    "values": []
  }

  for (var failIndex = 0; failIndex < size; failIndex++) {
    fails.values[failIndex] = {
      "id": failIndex,
      "color": "diceGray",
      "content": "",
      "marked": false
    }
  }

  game.rows[size] = fails;

  // Create colorBonus row
  if (settings.bonusMode === "colorBonus") {
    let colBonRow = {
      "id": "b1",
      "color": "diceRed",
      "marks": 0,
      "values": []
    }
  
    // let bonusArray = [0,1,2,3,2,0,3,1,0,1,3,2,1,2,3,0];
  
    for (var colorIndex = 0; colorIndex < maxCols; colorIndex++) {
      // console.log(bonusArray[colorIndex]);
      colBonRow.values[colorIndex] = {
        "id": colorIndex,
        "key": "bonus" + colorIndex,
        "color": gameColors.defaultColor[0][bonusArray[colorIndex]].color,
        "colorId": bonusArray[colorIndex],
        "content": "",
        "marked": false,
        "locked": true
      }
    }
  
    game.bonusRow = colBonRow;   
  }

  function getBonusMark(rowIndex, colIndex) {
    var retVal;

    let bonuses = [
      [1,4,7],
      [3,6,9],
      [1,5,8],
      [2,4,7]
    ];

    let longoBonuses = [
      [1,4,7,10],
      [3,6,9,12],
      [1,4,7,11],
      [2,5,8,10]
    ];

    if (maxCols === 12) {
      retVal = (settings.bonusMode === "colorBonus" && bonuses[rowIndex].indexOf(colIndex) !== -1) ? true : false;
    } else {
      retVal = (settings.bonusMode === "colorBonus" && longoBonuses[rowIndex].indexOf(colIndex) !== -1) ? true : false;
    }

    return retVal;
  }

  /**
   * Helper Method to generate value for the columns
   * @param {*} value Value that is used to calculate the value, usually the column index.
   * @param {*} type The type of creation that should be used. e.g. up, down, random.
   * @returns The value for the provided value index.
   */
  function genVal(value, type, rowIndex) {
    let retVal;
    switch (type) {
      case "mixxed":
        retVal = maxCols === 12 ? mixxedNumbers[rowIndex][value] : mixxedLongoNumbers[rowIndex][value];
        break;
      case "random":
        /* TODO unique value between 2 and 12 -> Math.random() not good */
        //retVal = Math.floor((Math.random() * (maxCols-2)) +2);
        retVal = gameNumbers[value];
        break;
      case "down":
        retVal = value + maxCols - (value*2);
        break;
      case "up":
      default:
        retVal = value + 2;
        break;
    }

    return retVal;
  }

  function getGameStyle(style) {
    let retVal;

    switch (style) {
      case "mixxedNumber":
        retVal = Array(size).fill("mixxed");
        break;
      case "randomNumber":
        retVal = Array(size).fill("random");
        break;
      case "default":  
      default:
        retVal = ["up", "up", "down", "down"];
        break;
    }

    return retVal;
  }

  return game;
}
